@import "./variables.scss";
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 13px;
}

a {
    cursor: pointer;
    text-decoration: none;
}

a:hover {
    opacity: 0.8;
}

.App {
    background-color: $background-primary;
    width: 100%;
    height: 100vh;
    outline: hidden;
}


::-webkit-scrollbar {
    height: 5px;
    display: none;
    cursor: pointer;
}
::-webkit-scrollbar-track {
    background: rgba($text-color-primary, 0.5);
}
::-webkit-scrollbar-thumb {
    background-image: linear-gradient($text-color-primary, $text-color-clair);
    border-radius: 2px;
}