.alice-carousel {
    position: relative;

    width: 100%;
    margin: auto;
    direction: ltr;
}

.alice-carousel__wrapper {
    position: relative;
    overflow-x: hidden;
    overflow-y: hidden;
    box-sizing: border-box;
    width: 100%;
    height: auto;
}

.alice-carousel__stage {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    white-space: nowrap;

    transform-style: flat;
    -webkit-transform-style: flat;
 
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;

    &-item {
        position: relative;

        display: inline-block;
        padding: 0;

        box-sizing: border-box;
        width: 11.5rem;
        height: 11.5rem;

        vertical-align: top;
        white-space: normal;
        outline: transparent;
        line-height: 0;
   
        &.__hidden {
            opacity: 0;
            overflow: hidden;
        }

        &__added {
            border-radius: 2rem;
            width: 100%;
            height: 100%;

            & .overlay {
                display: flex;
                width: 11.5rem;
                height: 11.5rem;
                background: linear-gradient(
                    182.07deg,
                    rgba(0, 0, 0, 0) 77.6%,
                    rgba(0, 0, 0, 0.75) 97.31%
                );
                position: absolute;
                border-radius: 2rem;
            }
            & img {
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 2rem;
                image-rendering: crisp-edges;
            }
        }

    }

    &-item:not(:last-child) {
        margin-right: 1rem;
    }

    
}

.alice-carousel__prev-btn,
.alice-carousel__next-btn {
    display: inline-block;

    box-sizing: border-box;
    width: 50%;
    padding: 10px 5px;

    [data-area] {
        &::after {
            position: relative;

            content: attr(data-area);
            text-transform: capitalize;
        }
    }
}

.alice-carousel__prev-btn {
    text-align: right;
}

.alice-carousel__prev-btn-item,
.alice-carousel__next-btn-item {
    display: inline-block;
    cursor: pointer;
    padding: 5px;
    margin: 0;
    &.__inactive {
        opacity: 0.4;
        pointer-events: none;
    }
}
