@import "../variables.scss";
@import "../mixin.scss";

.home-page {
    height: calc(100vh - #{$nav-bar-height});
    width: 100vw;
    outline: hidden;
    // margin-left: 2rem;
    // margin-right: 2rem;
    margin-top: $nav-bar-height;

    &__order-ways,
    &__products,
    &__other-info {
        height: calc(100% / 3);
    }

    // ORDER PART
    &__order-ways {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
    .option {
        width: calc(100% / 3);
        height: calc(100% / 2);
        border: white 0.2rem solid;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        animation: fadeInUp 0.5s;
        position: relative;

        .option-overlay {
            text-align: center;
            text-justify: center;
            p {
                padding: 1rem;
                font-size: 1.2rem;
            }
        }
        .option-information {
            display: flex;
            justify-content: space-evenly;
            display: none;
            @include center-vertically;

            & a {
                flex: 1;
                border: 1px solid white;
                border-radius: 10px;
                box-shadow: 0px 2px 7px rgb(145, 144, 144);
                margin: 0.5rem;
                padding: 0.15rem;
                display: flex;
                justify-content: center;
                align-items: center;
                @include respond(tab-land) {
                    margin: 3rem;
                }
                & img {
                    width: 100%;
                    @include respond(phone) {
                        width: 80%;
                    }
                    @include respond(tab-land) {
                        width: 50%;
                    }
                }
            }
        }

        &:hover .option-information,
        &:focus .option-information {
            display: flex;
            transition: all 1s;
        }

        &:hover .option-overlay,
        &:focus .option-overlay {
            animation: fadeOut 1s;
            opacity: 0;
            transform: opacity 0.1s 1s;
        }
    }

    // PRODUCTS PART
    &__products {
        h1 {
            margin-bottom: $margin-bottom-small;
        }
        margin-left: 2rem;
        margin-right: 2rem;
    }
}
