@import "../variables.scss";
.nav-bar {
    display: flex;
    align-items: center;
    justify-content: center;
    height: $nav-bar-height;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    background-color: $background-primary;

    .logo {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h1 {
            font-size: 2.5rem;
        }
        p {
            font-size: 1.1rem;
        }
    }
}
